*{
    margin: 0;
    padding: 0;
    }

    body{
        font-family: 'Comfortaa', cursive;
        
        color: black;
        overflow-x: hidden;
        }

.container{
    position: relative;
    width: 1280px;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;
    background: linear-gradient(45deg, #d299c2, #fef9d7);
    border-radius: 15px;  

    .gallery {
            
        
        .emotions {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-bottom: 35px;
        
            h1 {
            width: 100%;
            text-align: center;
            margin: 35px;
        }

            img {
                width: 150px;
            }
        }

        #header{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
            min-height: 70px;
            padding: 0 20px;

            #search_box{
                position: relative;
                width: 750px;

                input{
                    box-sizing: border-box;
                    height: 40px;
                    width: 100%;
                    font-size: 1rem;
                    padding: 8px;
                    border-radius: 25px;
                    outline: none;
                    
                    -webkit-transition: all 0.10s ease-in-out;
                    -moz-transition: all 0.10s ease-in-out;
                    -ms-transition: all 0.10s ease-in-out;
                    -o-transition: all 0.10s ease-in-out;
                    transition: all 0.10s ease-in-out;
                    border: 1px solid #DDDDDD;

                    &:focus{
                        box-shadow: 0 0 5px #cf6db3;
                        border: 1px solid #cf6db3;
                    }
                    }

                
                    i{
                        color: black;
                        position: absolute;
                        cursor: pointer;
                        top: 25%;
                        right: 4%;
                        transition: 1s;

                        &:hover{
                            color: #cf6db3;
                        }

                        }
                }
            
            }


    }
    

    
  }
  
  .grid {
    background: linear-gradient(45deg, #fef9d7, #d299c2);
    border-radius: 15px;

        #grid{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-evenly;
            align-items: flex-start;

                .img{
                    width: 400px;
                    height: 214px;
                    margin-top: 15px;
                    margin-bottom: 15px;
                    background-position: center;
                    background-size: cover;
                    border-radius: 2px;
                    cursor: pointer;

                    }
            }
  }


  .show {
    display: none;
  }


  @media(max-width:1280px){
    .container{width: 100%;}
   }
   
   @media(max-width:768px){
     #main{width: 95%;}
   
     .img{width: 100%;}
   
     #header{
       margin: 10px 0;
       padding: 20px;
       gap: 10px;
       justify-content: center;
     }
   
     #header h2{padding: 5px 0;}
   }
   
   @media(max-width:480px){
     #header h2{
       text-align: center;
       font-size: 1.5rem;
     }
   
     #search_box{
       width: 80%;
       margin: auto;
     }
   
     #search_box input{font-size: .7rem;}
   }



.header {
    display: flex;
    margin: 30px 49px;
    gap: 50px;

    &__nav {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-grow: 1;
    }

    &__link {
        margin-right: 61px;

        text-decoration: none;
        font-size: 1vw;
        line-height: 12px;
        letter-spacing: 0.2em;
        text-transform: uppercase;
        color: #333333;
    }

    &__link:hover {
        color: #C4C4C4;
    }
}


.container-nickname {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 3% 5%;
}

.container-info {
    margin: 3% 5%;
    padding: 3%;

    text-align: center;
}

.container-avatar {
    margin: 15%;
}

.container-info,
.container-calendar {
    width: 40%;

    background-color: rgba(182, 82, 179, .3);
    border-radius: 11px;
}

#month {
    font-size: 35px;
}

.profile {
    position: absolute;
    right: 0;
    top: 0;
    width: 15%;
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: rgb(189, 157, 203);
    text-decoration: none;
    color: white;
    border-radius: 5px;
    box-shadow: 200px 0 0 0 rgba(0, 0, 0, .3) inset;

    &:hover {
        box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    }

    #avatar-pic {
        width: 30%;
        margin-right: 5px;
        margin: 3%;

        border-radius: 100%;
    }

    #nickname {
        width: 50%;
        margin: 5%;

        text-align: center;
        font-size: 15px;
    }
}

.container-calendar {
    margin: 3% 5%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 25%;
    left: 50%;
    width: auto;
}

.date_number {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 3%;
}


.date_btn {
    margin: 3px 5px;
    width: 16.5%;

    border: none;
    background-color: rgba(139, 0, 139, 0.498);
    border-radius: 50%;

    &:hover {
        cursor: pointer;
        color: #C4C4C4;
    }
}

#avatar-pic {
    height: 50%;
    width: 50%;
    margin-right: 5px;
    margin: 3%;

    border-radius: 100%;
}

#nickname {
    width: 50%;
    margin: 5%;

    font-size: 35px;
}

input {
    width: 100%;
    height: 25px;

    border-radius: 5px;
    border: none;

    &::placeholder {
        font-weight: bold;
        opacity: .5;
        color: rgb(100, 100, 100);
        text-align: center;
    }
}

.invisible,
.date-mood {
    display: none;
}


.hint {
    display: flex;
    width: 100%;
    margin: 5px 0 10px 5px;

    font-size: 13px;
    color: #8D8D8D;
    text-align: left;
}

.click {
    background-color: rgb(46, 0, 139);
}

.btn {
    background-color: #81279f;
    width: 222px;
    height: 35px;
    margin: 3% 0;

    border-radius: 12px;
    border: none;
    box-shadow: transparent 0 0 0 3px, rgba(18, 18, 18, .1) 0 6px 20px;

    cursor: pointer;

    span {
        font-family: 'Roboto-Regular';
        color: #FFFFFF;
        font-size: 12px;
        line-height: 12px;
        letter-spacing: 0.2em;
        text-transform: uppercase;
    }

    &:hover {
        background-color: rgba(139, 0, 139, 0.498);
        box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    }
}




.form {
    border-radius: 35px;
    width: 60vw;
    padding: 60px;
    margin: 0 auto;
    height: 80vh;
    background: linear-gradient(45deg, #d299c2, #fef9d7);
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);

    &__body {
        position: relative;
    }

    // &__body::after{
    //     content: "";
    //     position: absolute;
    //     top: 0;
    //     left: 0;
    //     width: 100%;
    //     height: 100%;
    //     background: rgba(51, 51, 51, 0.7) url("../assets/Youtube_loading_symbol_1_\(wobbly\).gif") center / 50px no-repeat;
    //     // opacity: 0;
    //     // visibility: hidden;
    // }
    &__title {
        display: flex;
        justify-content: center;
        font-size: 1.5vw;
        line-height: 12px;
        letter-spacing: 0.2em;
        text-transform: uppercase;
        color: #333333;
        margin-bottom: 50px;
    }

    &__item {
        margin: 20px 0;

        .checkbox {
            display: flex;
            flex-direction: row;

            &__input {
                display: none;
            }

            &__input:checked+.checkbox__label::after {
                transform: scale(1);
            }

            &__label {
                font-size: 1vw;
                color: #333333;
                display: inline-flex;
                align-items: center;
                line-height: 140%;
                position: relative;
                cursor: pointer;

                span {
                    color: #333333;
                }

                a {
                    color: #623373;
                }
            }

            &__label::before {
                content: "";
                flex: 0 0 24px;
                align-self: flex-start;
                height: 24px;
                background-color: #FFFFFF;
                border-radius: 4px;
                margin-right: 10px;
            }

            &__label::after {
                content: "";
                background-color: #81279f;
                width: 16px;
                height: 16px;
                position: absolute;
                top: 4px;
                left: 4px;
                border-radius: 4px;
                transform: scale(0);
                transition: transform 0.5s ease 0s;
            }
        }
    }

    &__label {
        display: block;
        margin: 10px 0;
        font-size: 1vw;
        line-height: 12px;
        letter-spacing: 0.2em;
        text-transform: uppercase;
        color: #333333;
    }

    &__input {
        border-radius: 5px;
        padding: 0 20px;
        width: 95%;
        height: 50px;
        font-size: 1em;
        color: #333333;
    }

    &__button {
        border-radius: 12px;
        box-shadow: transparent 0 0 0 3px, rgba(18, 18, 18, .1) 0 6px 20px;
        background-color: #81279f;
        cursor: pointer;
        display: flex;
        flex-direction: row;
        width: 50%;
        justify-content: center;
        margin: 50px auto;
        padding: 1rem 1.2rem;
        text-decoration: none;
        transition: box-shadow .2s, -webkit-box-shadow .2s;
        border: 0;
        font-family: 'Roboto-Regular';
        color: #FFFFFF;
        font-size: 12px;
        line-height: 12px;
        letter-spacing: 0.2em;
        text-transform: uppercase;

    }

    &__button:hover {
        background-color: rgba(139, 0, 139, 0.498);
        box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    }

    ._error {
        box-shadow: 0 0 15px red;
    }
}

.form::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(51, 51, 51, 0.7) url("../assets/Youtube_loading_symbol_1_\(wobbly\).gif") center / 50px no-repeat;
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s ease 0s;
}

.form._sending::after {
    opacity: 1;
    visibility: visible;
}